h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.page {
  scroll-behavior: smooth;
  min-width: 320px;
  overflow-x: hidden;
}

.page * {
  box-sizing: border-box;
  transition: all .3s;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page:has(.page__menu:target) .top-bar__menu-link {
  opacity: 0;
  transform: scale(-1);
}

.page__body {
  background: #eee;
  margin: 0;
  padding: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translate(0);
}

.header {
  background-color: #2c2c2c;
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  align-content: start;
  width: 100%;
  height: 100vh;
}

@media (width <= 1024px) {
