.theme-changer {
  --color-text: #6c788b;
  --color-after: #6c788b;
  cursor: pointer;
  position: relative;
  color: var(--color-text) !important;
}

.theme-changer:after {
  content: "";
  transform-origin: 100% 100%;
  width: 100%;
  height: 1px;
  transition: transform .25s ease-in-out;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scale(0);
  background-color: var(--color-after) !important;
}

.theme-changer:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

